import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isCheckedAll: false,
    isLoading: false,
    siteMaps: [],
    sortBy: '',
    isOpenConfirmModal: false,
  },

  getters: {
    isCheckedAll(state) {
      return state.isCheckedAll;
    },

    isLoading(state) {
      return state.isLoading;
    },

    siteMaps(state) {
      return state.siteMaps;
    },

    sortBy(state) {
      return state.sortBy;
    },

    filteredSiteMaps(state) {
      return payload => {
        switch (payload) {
          case 'Success':
            return state.siteMaps.filter(siteMap => siteMap.errors === 0)
          case 'Errors':
            return state.siteMaps.filter(siteMap => siteMap.errors > 0)
          default: 
            return state.siteMaps;
        }
      }
    },

    howManyChecked(state) {
      return state.siteMaps.filter(siteMap => siteMap.checked).length;
    },

    isOpenConfirmModal(state) {
      return state.isOpenConfirmModal;
    }
  },

  mutations: {
    loading(state) {
      state.isLoading = !state.isLoading;
    },

    saveData(state, payload) {
      state.siteMaps = payload.result.sitemap.map(site => ({...site, checked: false, id: uuidv4()}));
    },

    checkAll(state) {
      state.isCheckedAll = !state.isCheckedAll;
      state.siteMaps = state.siteMaps.map(siteMap => ({...siteMap, checked: state.isCheckedAll}));
    },

    toggleCheckMark(state, id) {
      state.siteMaps = state.siteMaps.map(siteMap => {
        if (siteMap.id === id) {
          return {...siteMap, checked: !siteMap.checked}
        }
        return siteMap;
      })
    },

    changeSortBy(state, payload) {
      state.sortBy = payload;
    },

    deleteSiteMap(state, id) {
      state.siteMaps = state.siteMaps.filter(siteMap => siteMap.id !== id)
    },

    toggleConfirmModal(state) {
      state.isOpenConfirmModal = !state.isOpenConfirmModal;
    }
  },

  actions: {
    LOAD_DATA: async (context) => {
      context.commit('loading');
      
      await axios.get('https://semalt.net/dev/api/v1/example/test/')
      .then(response => context.commit('saveData', response.data));
      
      context.commit('loading')
    }
  },
  modules: {
  }
})
