<template>
  <div :class="{selectBy: true, 'selectBy--focus': isOpen}">

    <div 
      :class="{'selectBy__select': true, 'selectBy__select--focus': isOpen}"
      @click="toggleIsOpen"
    >

      <span>{{sortBy}}</span>

      <ul class="selectBy__list" v-if="isOpen">

        <li 
          class="selectBy__item"
          v-for="option in options"
          :key="option"
          @click="sortByChange"
        >
          {{option}}
        </li>

      </ul>

    </div>
  </div>
</template>

<script>
export default {
  props: ['options'],
  
  data() {
    return {
      isOpen: false,
      sortBy: this.options[0],
    }
  },

  computed: {
    sort() {
      return this.$store.getters.sortBy
    }
  },

  methods: {
    toggleIsOpen() {
      this.isOpen = !this.isOpen;
    },

    sortByChange({target}) {
      this.sortBy = target.textContent.trim();
      this.$store.commit('changeSortBy', this.sortBy);
    }
  }
}
</script>
