<template>
  <div class="filterByUrl">

    <input
      class="filterByUrl__input"
      @focus="toggleIsOpen" 
      
      type="text" 
      v-model="query" 
      placeholder="URL or its part"
    >

    <div class="filterByUrl__types" v-if="isOpen">

      <input 
        type="radio" 
        id="contains" 
        value="contains" 
        v-model="sortBy"
      >
      <label for="contains">Sitemap contains</label>

      <input 
        type="radio" 
        id="dontContains" 
        value="dontContains" 
        v-model="sortBy"
      >
      <label for="dontContains">Sitemap doesn't contain</label>

      <input 
        type="radio" 
        id="exact" 
        value="exact" 
        v-model="sortBy"
      >
      <label for="exact">Exact match</label>

      <div class="filterByUrl__buttons">
        <button 
          type="button" 
          class="filterByUrl__btn"
          @click="handleReset"
        >
          Reset
        </button>

        <button 
          type="button" 
          class="filterByUrl__btn"
          @click="handleApply"
        >
          Apply
        </button>
      </div>

    </div>
    
  </div>
</template>

<script>
export default {
  data() {
    return {
      query: '',
      isOpen: false,
      sortBy: '',
    }
  },
  methods: {
    toggleIsOpen() {
      this.isOpen = !this.isOpen;
    },
    handleReset() {
      this.toggleIsOpen();
      this.query = '';
      this.sortBy = '';
    },
    handleApply() {
      this.toggleIsOpen();
      //Вызов функции для сортировки !
    },
  }
}
</script>
