<template>
  <div>
    <table class="table">

      <thead>

        <tr> 
          <th>
            <div 
              :class="{
                'table__checkbox': true, 
                'table__checkbox--checked': checkedAll && siteMaps.every(siteMap => siteMap.checked),
              }"
              @click="checkAll"
            >
            </div>
          </th>
          <th>Sitemap ({{siteMaps.length}})</th>
          <th>Type</th>
          <th>Submitted</th>
          <th>Last check</th>
          <th>Status</th>
          <th>URLs</th>
          <th><p>Recrawl</p><p>sitemap</p></th>
          <th><p>Remove</p><p>sitemap</p> </th>
        </tr>

      </thead>


      <tbody v-if="siteMaps">

        <tr
          v-for="siteMap in siteMaps"
          :key="siteMap.id"
          :class="{checked: siteMap.checked}"
        >

          <Tds 
            :siteMap="siteMap"
          />

        </tr>

      </tbody>
      
    </table>
  </div>
</template>

<script>
import Tds from './Tds.vue';

export default {
  components: {
    Tds,
  },
  computed: {
    checkedAll() {
      return this.$store.getters.isCheckedAll;
    },

    sortBy() {
      return this.$store.getters.sortBy;
    },

    siteMaps() {
      return this.$store.getters.filteredSiteMaps(this.sortBy);
    },

    isOpenConfirmModal() {
      return this.$store.getters.isOpenConfirmModal;
    }
  },

  methods: {
    checkAll() {
      this.$store.commit('checkAll');
    },
  }
}
</script>

<style lang="scss" scoped>
  .checked {
    background: #E7F6FF;
  }
</style>